import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

import { registroPSA } from '../modelo_registroPSA/registroPSA';

//dato del calculo
import { datoA } from '../modelo_datoA/datoA';

@Component({
  selector: 'app-registro-pedi-semanal-al',
  templateUrl: './registro-pedi-semanal-al.page.html',
  styleUrls: ['./registro-pedi-semanal-al.page.scss'],
})
export class RegistroPediSemanalAlPage implements OnInit {

  //dato de calculo
  objeto12:datoA = { semana:"", total_costo:""}
  
  pruebas12: datoA[] = [];
  url3 = 'http://localhost/proG/server_api/menu_datoA.php';

  //datos 

  
  objeto:registroPSA = { Des_de_semana:"", mes:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:""}
  
  pruebas: registroPSA[] = [];
  url = 'http://localhost/proG/server_api/crear_registroPSA.php';
  url2 = 'http://localhost/proG/server_api/menu_registroPSA.php';
 
  disabledButton;

  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders
  ) { 
    this.listarRegistroPSA();
    this.listarRegistroDA();
   }

  ngOnInit() {
  }

  agregarRegistroPSA(obj :registroPSA): Observable<registroPSA>{
    return this.http.post<registroPSA>(this.url, JSON.stringify(obj))
  }

  async agregarSE() {
    this.agregarRegistroPSA(this.objeto)
    .subscribe((resp) =>{
      this.objeto.Des_de_semana='';
      this.objeto.mes='';
      this.objeto.lunes='';
      this.objeto.martes='';
      this.objeto.miercoles='';
      this.objeto.jueves='';
      this.objeto.viernes='';
      this.objeto.sabado='';
      this.objeto.domingo='';
      this.listarRegistroPSA();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }

  getRegistroPSA(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarRegistroPSA(){
    this.pruebas = [];
    this.getRegistroPSA().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas.push(res[i]);
      }
    })
  }

  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_PSA',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroPSA();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }

  abrirCrud(a){
    this.router.navigate(['/modsemanaal/'+a]);
  }


   /// datos visualizzar 
   getRegistroDA(): Observable<any>{
    return this.http.get(this.url3);
  }

  listarRegistroDA(){
    this.pruebas12 = [];
    this.getRegistroDA().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas12.push(res[i]);
      }
    })
  }
  
  async delDatos(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_DA',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroDA();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }


}
